@mixin form-table {
    padding: 7px 5px;
}

tr.wlbb-form {
    td, th {
        @include form-table();
    }
    /* th {
        @include form-table();
    } */
}

.wlbb-grid-col-2_5-3_5 {
    display: grid;
    grid-template-columns: 40% 60%;
}

.wlbb-form {
    & select.wlbb-input-error, & input.wlbb-input-error {
        box-shadow: 0px 0px 2px 2px #ff000069;
    }
}

.form-table td > label {
    margin-left: 15px;
}

/* *
 * LOADING SELECT BUTTON
 * */
@keyframes spin {
    0% {
        transform: rotateZ(0);
    }
    100% {
        transform: rotateZ(720deg);
    }
}
  
$color-def: #1ecd97;
$color: #298dde;
  
button.spin {
    max-width: 200px;
    max-height: 50px;   
    position: relative;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    padding: 0;
    width: 120px;
    height: 30px;
    border-radius: 17.5px;
    background-color: transparent;
    outline: 0;
    cursor: pointer;
    overflow: hidden;
    vertical-align: middle;
}
  
.spin {
    border: 2px solid $color;
    color: $color;
    font-weight: 600;
    transition: all 200ms ease-in-out;
  
    span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        transition: opacity 200ms ease-in-out;
    
        &:last-child {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
        }
    }
    
    svg {
        height: 30px;
        fill: $color;
    }
    
    &:hover {
        background-color: $color;
        color: #fff;
    }
    
    &.done, &.processing {
        pointer-events: none;
        cursor: default;
    }
    
    &.processing {
        width: 30px;
        border-width: 4px;
        border-right-color: #bbb;
        border-bottom-color: #bbb;
    
        animation: spin;
        animation-delay: 200ms;
        animation-duration: 2s;
        animation-timing-function: ease-in-out;
        animation-iteration-count: 1;
    
        &:hover {
            background-color: transparent;
        }
    
        span:first-child {
            opacity: 0;
        }
    }
    
    &.done {
        &:hover {
            background-color: transparent;
        }
    
        span:first-child {
            opacity: 0;
        }
    
        span:last-child {
            opacity: 1;
        }
    }
}
  