table.wlbb-table {
	width: 100%;
	border-spacing: 5px;
	text-align: left;
	border-bottom: 1px solid #ccc;

	&.dataTable {
		margin-top: 10px;

		& thead th {
			border-bottom: 1px solid #ccc;

			&.sorting.sorting_asc,
			&.sorting.sorting_desc {
				border-bottom: 2px solid #9ca2a7;
			}
		}
	}

	&,
	& th,
	& td {
		border: 1px solid #ccc;
		border-collapse: collapse;
		padding: 10px;
	}

	& th {
		background-color: #f5f5f5;
	}
}

.text-center {
	text-align: center;
}

div.dataTables_wrapper.no-footer .dataTables_scrollBody {
    border-bottom: 1px solid #ccc;
}

