:root {
    --body-bg-color: #e3e3e3;
    --scroll-color: #a7a6a6;
    --scroll--hover-color: #298dde;
    --scroll-track-color: rgba(255,255,255,0);
}

.small-scroll::-webkit-scrollbar-track {
    background: linear-gradient(to right, var(--scroll-track-color) 0%, var(--scroll-track-color) 45%, var(--scroll-color) 50%, var(--body-bg-color) 61%, var(--body-bg-color) 100%);
}
  
.small-scroll::-webkit-scrollbar {
    width: 15px;
    transition: all .5s linear;
}
  
.small-scroll::-webkit-scrollbar-thumb {
    background-color: var(--scroll-color);
    border-radius: 7.5px;
}

.small-scroll:hover::-webkit-scrollbar-thumb {
    background-color: var(--scroll--hover-color);
    border-radius: 15px;
}

.wlbb-buttons-meta-container {
    width: 255px;
    height: 300px;
}

.scrollbar {
    padding: 15px;
    height: 250px;
    overflow-y: scroll;
    background: #ffffff;
    border: 1px solid #ececed;
    /*#f6f7f7 #c3bebe*/
    scrollbar-color: black #c3bebe;
    scrollbar-width: thin;
}
  
.force-overflow {
    min-height: 251px;
}

.wlbb-button-key-container {
    display: flex;
    margin-bottom: 5px;

    & > label {
        font-size: 14px;
        padding-left: 10px;
    }
}